* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}
body {
  background-color: rgb(45, 218, 218);
}
.container {
  max-width: 600px;
  margin: auto;
  text-align: center;
  margin-top: 30px;
  padding: 15px;
}
.theline {
  width: 100px;
  margin: auto;
  text-align: center;
  height: 6px;
  background-color: rgba(125, 125, 216, 0.788);
  position: relative;
  border-radius: 5px;
  overflow: hidden;
  margin-top: 20px;
}
.header h1 {
  font-size: 30px;
  font-weight: bold;
  color: rgb(44, 40, 40);
}
.reviews {
  background-color: white;
  padding: 15px;
  margin-top: 50px;
  border-radius: 5px;
  text-align: center;
  box-shadow: rgb(44, 40, 40) 5px 5px 10px;
}
.reviews div {
  margin-top: 10px;
}
.imgContainer {
  width: 130px;
  height: 130px;
  margin: auto;
  position: relative;
}

.imgContainer img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  position: relative;
}
.imgContainer::before {
  content: '';
  width: 130px;
  height: 130px;
  left: 10px;
  background-color: rgb(126, 126, 221);
  position: absolute;
  border-radius: 50%;
  top: -6px;
}

.quots {
  position: absolute;
  width: 40px;
  height: 40px;
  padding: 10px;
  border-radius: 50%;
  background-color: rgb(45, 218, 218);
  text-align: center;
  top: -10px;
  color: white;
  font-size: 18px;
  font-weight: bold;
}
.author {
  width: 100%;
  text-align: center;
  margin: auto;
}
.author h2 {
  font-size: 18px;
  text-transform: capitalize;
  word-spacing: 6px;
  letter-spacing: 2px;
}
.author h3 {
  font-size: 12px;
  text-transform: capitalize;
  /* word-spacing: 6px; */
  color: rgb(45, 218, 218);
  font-weight: 900;
  letter-spacing: 2px;
}
.info p {
  line-height: 25px;
  font-size: 13px;
  color: rgb(126, 123, 123);
  font-weight: 600;
}
.buttonsContainer {
  width: 100px;
  margin: auto;
  display: flex;
  justify-content: space-between;
}
.buttonsContainer button {
  font-size: 25px;
  color: rgb(45, 218, 218);
  border-width: transparent;
  border-color: transparent;
  font-weight: 900;
  background-color: inherit;
}
.buttonsContainer button:hover {
  color: rgb(7, 163, 163);
}

.surpriseMe button {
  color: rgb(45, 218, 218);
  border-width: transparent;
  border-color: transparent;
  background-color: inherit;
  font-size: 15px;
  border: rgb(7, 163, 163) 1px solid;
  border-radius: 5px;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-weight: bold;
}
.surpriseMe button:hover {
  background-color: rgb(7, 163, 163);
  transition: 0.5s;
  color: rgb(6, 75, 75);
}
